<script>
export default {
  data() {
    return {
      adminInfo: {},
      overviewStats: [
        { label: '‰ªäÊó•ËÆ¢Âçï', value: 0, icon: 'üìä' },
        { label: '‰ªäÊó•ÈîÄÂîÆÈ¢ù', value: '¬•0', icon: 'üí∞' },
        { label: 'ÂæÖÂ§ÑÁêÜËÆ¢Âçï', value: 0, icon: '‚è≥' },
        { label: 'ÊÄªÁî®Êà∑Êï∞', value: 0, icon: 'üë•' }
      ],
      recentActivities: [
        { text: 'Êñ∞ËÆ¢Âçï #TEA123456', time: '2ÂàÜÈíüÂâç', icon: 'üìã' },
        { text: 'Áî®Êà∑Ê≥®ÂÜå: Âº†‰∏â', time: '5ÂàÜÈíüÂâç', icon: 'üë§' },
        { text: 'ÂïÜÂìÅ‰∏äÊû∂: Êñ∞ÂìÅÂ•∂Ëå∂', time: '10ÂàÜÈíüÂâç', icon: 'üçπ' },
        { text: 'ËÆ¢ÂçïÂÆåÊàê: #TEA123455', time: '15ÂàÜÈíüÂâç', icon: '‚úÖ' }
      ]
    }
  },
  onLoad() {
    this.loadAdminInfo();
    this.loadStatistics();
    this.loadRecentActivities();
  },
  methods: {
    // Âä†ËΩΩÁÆ°ÁêÜÂëò‰ø°ÊÅØ
    loadAdminInfo() {
      const adminInfo = uni.getStorageSync('adminInfo');
      if (adminInfo) {
        this.adminInfo = adminInfo;
      }
    },
    
    // Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆ
    async loadStatistics() {
      try {
        console.log('Making request to statistics API...'); // Ë∞ÉËØï‰ø°ÊÅØ
        const res = await uni.request({
          url: 'http://localhost:3000/api/admin/statistics',
          method: 'GET',
        });
        
        console.log('Response:', res); // Ë∞ÉËØï‰ø°ÊÅØ
        
        if (res.data.success) {
          const stats = res.data.statistics;
          this.overviewStats[0].value = stats.todayOrders;
          this.overviewStats[1].value = `¬•${stats.todaySales}`;
          this.overviewStats[2].value = stats.pendingOrders;
          this.overviewStats[3].value = stats.totalUsers;
        } else {
          console.error('API Error:', res.data);
        }
      } catch (error) {
        console.error('Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆÂ§±Ë¥•:', error);
      }
    },

    // Âä†ËΩΩÊúÄËøëÊ¥ªÂä®
    async loadRecentActivities() {
      try {
        const res = await uni.request({
          url: 'http://localhost:3000/api/admin/recent-activities',
          method: 'GET'
        });
        if (res.data && res.data.success) {
          this.recentActivities = res.data.activities.map(item => ({
            ...item,
            // Ê†ºÂºèÂåñÊó∂Èó¥ÔºàÂ¶Ç‚Äú2ÂàÜÈíüÂâç‚ÄùÔºâ
            time: this.formatTime(item.time)
          }));
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÊúÄËøëÊ¥ªÂä®Â§±Ë¥•:', error);
      }
    },
    // Êó∂Èó¥Ê†ºÂºèÂåñËæÖÂä©ÊñπÊ≥ï
    formatTime(time) {
      const t = new Date(time);
      const now = new Date();
      const diff = Math.floor((now - t) / 1000);
      if (diff < 60) return `${diff}ÁßíÂâç`;
      if (diff < 3600) return `${Math.floor(diff/60)}ÂàÜÈíüÂâç`;
      if (diff < 86400) return `${Math.floor(diff/3600)}Â∞èÊó∂Ââç`;
      return t.toLocaleString();
    },

    // ÈÄÄÂá∫ÁôªÂΩï
    logout() {
      uni.showModal({
        title: 'Á°ÆËÆ§ÈÄÄÂá∫',
        content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            uni.clearStorageSync();
            uni.redirectTo({ url: '/pkg_user/login/login' });
          }
        }
      });
    },

    // Ë∑≥ËΩ¨Âà∞ËÆ¢ÂçïÁÆ°ÁêÜ
    goToOrders() {
